 * @FilePath: \music\src\pages\Song.vue
-->
<template>
  <div class="song">
    <!-- 遮罩层 -->
    <div class="bgmask"></div>
    <!-- 背景图 -->
    <div class="bg" :style="{'background-image': 'url('+songUrl+')','background-color': 'rgb(255,255,255)'}"></div>
    <!-- 主体 -->
    <div class="music-container">
         <!-- 歌曲列表 -->
         <div class="content">
           <!-- 歌曲列表 -->
           <div class="song-list">
             <div class="song-container">
                 <div class="item">
                   <span class="song-name">歌名</span>
                   <span class="singer-name">歌手</span>
                   <span class="time">时长</span>
                 </div>
                 <div class="item" v-for="(item, index) in songs" :key="index">
                   <span :class="['song-name',  item_index==index? 'song-active': '']" @click="item_index=index">
                     {{item.name}}
                     <img src="/img/loading-svg/loading-bars.svg" v-show="index==item_index && play" />
                   </span>
                   <span class="singer-name" @click="goSingerDetail(item.ar[0].id, item.ar[0].name)">{{item.ar[0].name}}</span>
                   <span class="time">{{item.format_dt}}</span>
                 </div>
             </div>
           </div>
           <!-- 进度条 -->
           <div class="prograss">
               <div class="left">
                  <div class="iconfont item icon-shangyishouge" @click="item_index--"></div>
                  <div :class="['item','iconfont',play? 'icon-zanting': 'icon-bofang']" @click="isplay"></div>
                  <div class="iconfont item icon-kuaijin" @click="item_index++"></div>
               </div>
               <div class="prograss-container">
                  <span class="songname" @click="scoll">{{songname}}</span>
                  <div class="current-time"></div>
                  <div class="bar" @mousedown="barDown"></div>
               </div>
               <audio :src="url" ></audio>
           </div>
         </div>
         <!-- 侧栏 -->
         <div class="aside">
           <!-- 歌曲信息 -->
           <div class="current-song">
             <img v-lazy="songUrl" :class="[play? 'animation-rotate': '']"/>
           </div>
           <!-- 歌词 -->
           <div class="lyric">
               <div class="lrc-container" v-if="lrcFlag">
                  <span  v-for="(item, index) in parseLyric" :key="index" :class="['lrc', index==active? 'active': '']">{{item.lrc}}</span>
               </div>
               <div class="lrc-container" v-if="!lrcFlag " style="top: 3px;">
                    <span class="lrc">暂无歌词</span>
               </div>
           </div>
         </div>
    </div>
  </div>
</template>
<script>
// 工具包
import {
  req_lyric,
  req_song_url,
  req_song_detail,
} from "../network/song/index.js";
import { req_search } from "../network/search/index.js";
import tools from "../tools/formatData.js";
export default {
  name: "song",
  components: {},
  data() {
    return {
      // 用户是否未第一次打开
      first: true,
      // 是否播放
      play: false,

      // 当前播放的歌曲
      item_index: 0,
      // 当前播放的歌曲（默认第一首）
      id: 0,
      // 当前播放音频
      url: "",
      // 当前歌曲背景图
      songUrl: "",
      // 当前播放歌曲名字
      songname: "",
      // 当前播放的时长
      songTime: 0,
      // 歌单图片（背景图）
      cover: "",
      // 歌词处理后
      parseLyric: [],
      // 当前激活歌词
      active: 0,
      // 是否有歌词
      lrcFlag: true,

      // 歌单中所有歌曲 id
      trackIds: [],
      // 歌单中所有歌曲信息
      songs: [],
      // 歌单总数
      songNum: 0,

      // 进度条是否按下
      down: false,
      // 实际进度条长度
      prograssDistance: 0,
      // 当前进度条滑动的距离
      currentDistance: 0,
      // 当前歌曲时间
      timeStamp: 0,
      // 实际滚动距离
      x: 0,
    };
  },
  watch: {
    // 歌单数据可视化（时长）
    songs: function() {
      this.songs.forEach((e, index, arr) => {
        let value = tools._msTos(e.dt);
        arr[index].format_dt = value;
      });
    },
    // 是否播放
    play: function() {
      let audio = document.querySelector("audio");
      if (this.play) {
        // 音频是否存在
        if (this.url) {
          audio.play();
          // 判断是否第一次打开
          if (this.first) this.first = false;
        } else {
          this.$message({
            message: "亲爱的,暂无版权",
            type: "error",
          });
          // 重置
          this.reset();
        }
      } else {
        audio.pause();
      }
    },
    // 时间戳
    timeStamp: function() {
      // 更新进度条
      let x = this.timeStamp * (this.prograssDistance / this.songTime);
      document.querySelector(".bar").style.left = x + "px";
      document.querySelector(".current-time").style.width = x + "px";
      // 保存实际滚动距离
      this.x = x;
    },
    // 上一首 下一首
    item_index: function() {
      // 重置
      this.reset();

      // 范围判断
      let value = this.item_index;
      if (value < 0) this.item_index = 0;
      if (value >= this.songNum) this.item_index = this.item_index - 1;

      //  取出数据
      this.songname = this.songs[this.item_index].name;
      this.id = this.songs[this.item_index].id;
      this.songTime = this.songs[this.item_index].dt;

      //  发送请求
      this.getsongUrl();
      this.getSongImg();
      this.getlyric();
    },
  },
  created() {
    this.getTrackIdsandInit();
  },
  mounted() {
    this.prograssInit();
  },
  methods: {
    // 处理歌单 id
    disposeId() {
      let end = window.localStorage.getItem("trackCount");
      this.songNum = end;
      let arr = this.trackIds.slice(0, end);
      let str = tools._arrToString(arr);
      return str;
    },
    // 从缓存中获取 trackIds 并初始化第一首歌
    getTrackIdsandInit() {
      this.trackIds = JSON.parse(window.localStorage.getItem("playList"));
      this.cover = window.localStorage.getItem("playListCover");
      
      // 处理 id 并获取歌单中所有歌曲信息
      let ids = this.disposeId();
       // 获取歌单信息
      this.getsongdetail(ids);


      // 初始化 （默认取出第一首）
      this.id = this.trackIds[0].id;
      this.getsongUrl();
      this.getlyric();
     
    },
    // 获取音频
    getsongUrl() {
      req_song_url(this, { id: this.id })
        .then(res => {
          let url = res.data[0].url;
          if (url) {
            this.url = url;
            // 初始化 audio
            this.audioInit();
          } else {
            this.$message({
              message: "暂无版权,已为你跳到下一首",
              type: "error",
            });
            this.item_index++;
          }
        }).catch(error => {
          console.log("获取音频可能出现错误",error);
        });
    },
    // 获取歌词
    getlyric() {
      req_lyric(this, { id: this.id })
        .then(res => {
          let value = tools._parseLyric(res.lrc.lyric);
          if (value.length == 0) {
            this.lrcFlag = false;
          } else {
            this.lrcFlag = true;
            this.parseLyric = value;
          }
        })
        .catch(error => {
          console.log("获取歌词可能出现错误",error);
        });
    },
    // 获取歌曲信息
    getsongdetail(ids) {
      req_song_detail(this, {
        ids,
      }).then(res => {
        this.songs = this.songs.concat(res.songs);

        // 初始化第一首歌名字
        this.songname = this.songs[0].name;
        // 初始化第一首歌时长
        this.songTime = this.songs[0].dt;
        // 获取第一首歌图像
        this.getSongImg();
      }).catch(error => {
        console.log("获取歌曲信息可能出现错误",error);
      });
    },
    // 获取歌曲图片
    getSongImg() {
      req_search(this, {
        keywords: this.songname,
      }).then(res => {
          if (res.result.songs) {
            this.songUrl = res.result.songs[0].al.picUrl;
          } else {
            this.songUrl = this.cover;
          }
        }).catch(error => {
          console.log("获取歌曲图片",error);
        })
    },

    // bar 监听
    barDown() {
      // 允许拖拽
      this.down = true;
      // 音频暂停
      this.play = false;
    },
    // 滚动条监听
    prograssInit() {
      // 保存进度条长度
      this.prograssDistance =
        document.querySelector(".prograss-container").offsetWidth -
        document.querySelector(".bar").offsetWidth;

      // 监听进度条拖拽
      document.onmousemove = e => {
        if (this.down) {
          let x = e.pageX;
          let bar = document.querySelector(".bar");
          // 进度条容器
          let container = document.querySelector(".prograss-container");
          let current = document.querySelector(".current-time");
          let prograss_left = document.querySelector(".prograss").offsetLeft;
          let container_left = container.offsetLeft;
          let container_width = container.offsetWidth;
          let bar_width = bar.offsetWidth;
          // 移动的距离
          let distance = x - container_left - prograss_left - bar_width / 2;

          // 判断是否超出范围
          distance = distance < 0 ? 0 : distance;
          distance =
            distance > container_width - bar_width
              ? container_width - bar_width
              : distance;

          // 设置样式
          bar.style.left = distance + "px";
          current.style.width = distance + "px";

          // 保存当前滑动的距离
          this.currentDistance = distance;
        }
      };
      // 停止拖拽
      document.onmouseup = () => {
        if (this.down) {
          // 停止拖拽
          this.down = false;
          // 更新当前音频时间
          this.currentTime();
        }
      };
    },
    // 根据距离换算当前歌曲时间
    currentTime() {
      let audio = document.querySelector("audio");
      let x =
        this.currentDistance * (this.songTime / this.prograssDistance) / 1000;
      audio.currentTime = x;
      // 打开音频播放
      this.play = true;
    },
    // 音频播放中
    audioTimeUpdate() {
      let audio = document.querySelector("audio");
      let lrcContainer = document.querySelector(".lrc-container");
      // 获取此时播放时间
      let currentTime = audio.currentTime;
      // 更新时间戳
      this.timeStamp = currentTime * 1000;

      // 匹配歌词
      let value = ("" + currentTime).split(".")[0];
      for (let i = 0; i < this.parseLyric.length; i++) {
        if (("" + this.parseLyric[i].time).split(".")[0] == value) {
          this.active = i;
          let lrc = document.querySelectorAll(".lrc")[i];
          lrcContainer.style.top = 100 - lrc.offsetTop + "px";
          break;
        }
      }
    },
    // 音频结束
    audioEnded() {
      this.play = false;
      // 进度条到底
      this.timeStamp = this.songTime;
      // 重置
      this.reset();
      // 下一首
      this.item_index++;
    },
    // 音频加载完毕
    audioCanplay() {
      if (!this.first) {
        this.play = true;
      }
    },
    // 音频监听
    audioInit() {
      let audio = document.querySelector("audio");
      // 移除上一次监听器
      audio.removeEventListener("timeupdate", this.audioTimeUpdate);
      audio.removeEventListener("ended", this.audioEnded);
      audio.removeEventListener("canplaythrough", this.audioCanplay);

      // 播放中
      audio.addEventListener("timeupdate", this.audioTimeUpdate);
      // 播放结束
      audio.addEventListener("ended", this.audioEnded);
      // 加载完毕
      audio.addEventListener("canplaythrough", this.audioCanplay);
    },

    // 播放暂停
    isplay() {
      this.play = !this.play;
    },
    // 跳转
    goSingerDetail(id, name) {
      this.$router.push({
        path: `/singerDetail/${new Date().getTime()}`,
        query: {
          id,
          name,
        },
      });
    },
    // 重置
    reset() {
      // 重置
      let lrcContainer = document.querySelector(".lrc-container");
      lrcContainer.style.top = "0px";
      this.timeStamp = 0;
      this.play = false;
    },
    // 滚轮
    scoll() {
      let target = document.querySelectorAll(".song-container > .item")[
        this.item_index + 1
      ];
      if (target.offsetTop > 500) {
        document.querySelector(".song-list").scrollTop = target.offsetTop - 100;
      }
    },

  },
  beforeDestroy() {
    // 关闭歌曲
    this.play = false;
  },
};
</script>
<style lang="scss" scoped>
@import "../assets/scss/music.scss";
@import "../assets/scss/icons.scss";
@import "../assets/scss/animate.scss";
.song {
  position: relative;
  z-index: 3;
  height: 720px;
  .bg {
    position: absolute;
    @include bgmask();
    width: 100%;
    height: 100%;
    overflow: hidden;
    z-index: -1;
  }
  .bgmask {
    width: 100%;
    height: 100%;
    position: absolute;
    top: 0;
    left: 0;
    background-color: rgba(0, 0, 0, 0.35);
    z-index: -2;
  }
  .music-container {
    z-index: 3;
    @include flex();
    padding-top: 80px;
    box-sizing: border-box;
    overflow: hidden;
    .content {
      margin-right: 20px;
      flex: 4;
      height: 600px;
      .song-list {
        height: 500px;
        overflow: auto;
        .song-container {
          .item {
            height: 50px;
            color: hsla(0, 0%, 88.2%, 0.8);
            @include font(16px, 50px, $colorD);
            padding-left: 10px;
            box-sizing: border-box;
            @include flex();
            text-align: left;
            .song-name {
              flex: 2;
              @include fontHidden();
              margin-right: 12px;
              @include flex(flex-start);
              img {
                margin-left: 30px;
                margin-top: -5px;
                width: 100px;
                height: 50px;
              }
            }
            .singer-name {
              flex: 1;
              @include fontHidden();
              margin-right: 12px;
            }
            .time {
              width: 79px;
              height: 100%;
              @include fontHidden();
            }
            .song-active {
              color: $colorA;
            }
            &:nth-child(n + 2) {
              span {
                &:hover {
                  color: $colorA;
                  cursor: pointer;
                }
              }
            }
          }
        }
      }
      .prograss {
        position: relative;
        margin-top: 20px;
        height: 90px;
        display: flex;
        justify-content: flex-start;
        padding: 10px 20px;
        box-sizing: border-box;
        .left {
          display: flex;
          justify-content: flex-start;
          margin-right: 20px;
          margin-top: 13px;
          .item {
            width: 100px;
            height: 40px;
            margin-right: 10px;
            background-repeat: no-repeat;
            background-size: 40px 40px;
            background-position: 10px 10px;
            &:hover {
              color: $colorA;
            }
            &:last-child {
              margin-right: 0;
              padding-top: 5px;
              box-sizing: border-box;
              margin-left: -30px;
            }
            &:nth-child(2) {
              font-size: 40px;
            }
            &:nth-child(1) {
              padding-top: 5px;
              box-sizing: border-box;
              padding-left: 40px;
            }
          }
        }
        .prograss-container {
          position: absolute;
          top: 0;
          bottom: 0;
          right: 39px;
          margin-top: auto;
          margin-bottom: auto;
          width: 600px;
          height: 6px;
          background-color: $colorD;
          border-radius: 40px;
          .songname {
            position: absolute;
            top: -36px;
            left: 0px;
            @include font(14px, 20px, $colorD);
            cursor: pointer;
            &:hover {
              color: $colorA;
            }
          }
          .current-time {
            position: absolute;
            top: 0px;
            left: 0px;
            width: 0px;
            height: 100%;
            background-color: $colorA;
            background-image: linear-gradient(
              to right,
              #43e97b 0%,
              #38f9d7 100%
            );
          }
          .bar {
            position: absolute;
            top: -5px;
            left: 0px;
            width: 16px;
            height: 16px;
            border-radius: 50%;
            background-color: $colorA;
            background-image: linear-gradient(
              to right,
              #43e97b 0%,
              #38f9d7 100%
            );
          }
        }
      }
    }
    .aside {
      flex: 1;
      height: 600px;
      .current-song {
        height: 200px;
        @include flex(center);
        img {
          width: 200px;
          border-radius: 50%;
          display: block;
        }
        .animation-rotate {
          animation-name: rotate;
          animation-duration: 6s;
          animation-timing-function: linear;
          animation-iteration-count: infinite;
        }
      }
      .lyric {
        margin-top: 12px;
        height: 427px;
        text-align: center;
        position: relative;
        @include font(14px, 30px, $colorD);
        color: hsla(0, 0%, 88.2%, 0.8);
        overflow: hidden; 
        .lrc-container {
          width: 200px;
          text-align: center;
          position: absolute;
          left: 0;
          right: 0;
          top: 100px;
          margin-left: auto;
          margin-right: auto;
          .lrc {
            display: block;
            margin-top: 15px;
          }
          .active {
            color: $colorA;
          }
        }
      }
    }
  }
}
// 隐藏浏览器掉滚轮
html {
  -ms-overflow-style: none;
  overflow: -moz-scrollbars-none;
}
html::-webkit-scrollbar {
  width: 0px;
}
// 隐藏滚动条
.song-list::-webkit-scrollbar {
  width: 6px;
  height: 6px;
}
.song-list::-webkit-scrollbar-track {
  background-color: rgba(0, 0, 0, 0.1);
  border-radius: 2em;
}
.song-list::-webkit-scrollbar-thumb {
  background-color: rgba(0, 0, 0, 0.4);
  border-radius: 2em;
}
.song-list::-webkit-scrollbar-corner {
  background-color: transparent;
}
</style>